.table{background-color:#fff;box-shadow:0px 12px 26px rgba(16,30,115,.06);border-radius:8px}.table h5{margin:24px}.table table{width:100%;text-align:left;border-spacing:0}.table table thead{background-color:rgba(232,232,232,.2)}.table table thead tr th{font-weight:700;font-size:.875rem;line-height:1.25rem;letter-spacing:.1px;padding:20px;opacity:1}.table table tbody tr td{padding:15px;font-weight:400;font-size:.875rem;line-height:1.25rem;letter-spacing:.1px;border-top:1px solid #e8e8e8}.table table tbody tr td:first-child{display:-webkit-flex;display:flex;-webkit-justify-content:inherit;justify-content:inherit;-webkit-align-items:center;align-items:center;-webkit-flex-direction:row;flex-direction:row}.table table tbody tr td:first-child img{margin-right:10px}.table table tbody tr td .image-name{display:-webkit-flex;display:flex;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-align-items:center;align-items:center;-webkit-flex-direction:row;flex-direction:row}.table table tbody tr td .image-name img{margin-right:12px}.table table tbody tr td:last-child .image-name img{cursor:pointer}
.modal{display:-webkit-flex;display:flex;-webkit-justify-content:space-evenly;justify-content:space-evenly;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column;height:95%}.modal .modal-buttons{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center}.modal .modal-buttons button{background:#336cfb;border:1px solid #336cfb;outline:none;cursor:pointer;color:#fff;padding:10px;border-radius:4px;cursor:pointer}.modal .modal-buttons button:hover{background:none;border:1px solid #e8e8e8;outline:none;cursor:pointer;color:#000}.modal .modal-buttons button:first-child{margin-right:5px}.modal .modal-buttons .MuiSnackbarContent-root{background-color:blue}.modal form{display:-webkit-flex;display:flex;-webkit-justify-content:inherit;justify-content:inherit;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column}.modal form .rows{display:-webkit-flex;display:flex}.modal form .rows .label-input{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column}.modal form .rows .label-input fieldset{border:none}.modal form .rows .label-input input{outline:none;width:300px;height:50px;margin:20px;border:1px solid #336cfb;border-radius:10px;padding:0}.modal form .rows .label-input input::-webkit-input-placeholder{color:#336cfb;opacity:.7;font-style:italic}.modal form .rows .label-input input:-ms-input-placeholder{color:#336cfb;opacity:.7;font-style:italic}.modal form .rows .label-input input::placeholder{color:#336cfb;opacity:.7;font-style:italic}.modal form .rows .label-input input label{display:none}.modal form .rows .label-input .input-errors{color:red;font-size:15px;margin:-10px 0 15px 0px}
