slate(-8px, -10px);transform:rotate(45deg) translate(-8px, -10px)}body .search-and-notification .search{display:none}body .search-and-notification .search img{margin-left:50px}body .Sidebar.active{left:0;width:100%;height:100vh;z-index:1111}body .Sidebar{transition:.7s;position:fixed;top:0;left:-100%}body .Sidebar .search{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-flex-direction:row;flex-direction:row}body .Sidebar .logo img:first-child{display:block}body .Sidebar .logo img:last-child{display:none}body .Sidebar li{padding:5px}body .Sidebar li a{display:-webkit-flex;display:flex;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-align-items:center;align-items:center;-webkit-flex-direction:row;flex-direction:row}body .Sidebar li a span{display:block}body .table-wrapper table{overflow:scroll}}@media only screen and (max-width: 350px){body .search-and-notification .hamburger-menu{margin-left:7px;-webkit-align-items:flex-start;align-items:flex-start}body .search-and-notification .hamburger-menu .hamburger-line{width:20px}body .search-and-notification .notification-profile{width:50%}}
